
*
{
  padding: 0;
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 200;
  font-style: normal;
  margin-top: %;
  
}
.bg
{
  background: linear-gradient(rgba(21, 5, 236, 0.5), rgba(247, 2, 194, 0.5)),
  url(https://png.pngtree.com/tarch-bar-with-magnifier-icon-searching-image_13512460.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
  position: fixed;
}


.input-container .mid-input-box .place {
  color: aliceblue;
}



.input-container
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.input-container h1
{
  margin-top: 5%;
  color: rgb(231, 255, 12);
  font-size: 55px;
}
/* .input-container */
.input-container .mid-input-box
{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.input-container .mid-input-box input
{
  background-color: transparent;
  border: 2px solid rgb(252, 252, 252);
  text-align: left;
  padding: 10px;;
  font-size: 20px;
  height: 30px;
  border-radius: 10px;
  color: white;
}
.input-container .mid-input-box input:hover
{
  border: 2px solid rgb(179, 255, 0);
}
.input-container .mid-input-box input::placeholder
{
  color: rgb(255, 255, 255);
}
.input-container .mid-input-box input:focus
{
  border: 2px solid green;
  outline: none;
}
.input-container .mid-input-box button
{
  background-color: rgb(70, 202, 18);
  color: white;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  font-size: 20px;
  height: 50px;
  border-radius: 10px;
}
.input-container .mid-input-box button:hover
{
  background-color: orangered;
}
.input-container .mid-input-box button:active
{
  background-color: green;
}

.input-container nav li{
  background-color: blue;
}

/* Navbar */
nav
{
  background-color: transparent;
  /* backdrop-filter: blur(10px); */
  padding: 15px 3%;
  margin-top: 50px;
}
nav ul
{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav ul li
{
  color: white;
  cursor: pointer;
  font-size: 22px;
  list-style-type: none;
  border-bottom: 3px solid transparent;
}
nav ul li:hover
{
  border-bottom: 3px solid white;
  transition: 3s;
}
.navActive
{
  color: red;
  transform: scale(1.1);
  border-bottom: 3px solid red;
}
.navActive:hover
{
  border-bottom: 3px solid rgb(255, 238, 2);
}


/* section */
h4
{
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(255, 255, 255);
}
.section
{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}